@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5;
    background: blueviolet;
    -webkit-font-smoothing: antialiased;
}

.container-pokemon{
    display: flex;
}
.container-header{
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 120px;
  background-color: #EE0D18;

  box-shadow: 0px 0px 15px 5px #FDD824;
}

.img-header img{
  align-items: center;
}

.size-img-pikachu{
  width: 120px;
  height: 100px;
  margin-left: 30px;
}

.size-img-pokestore{
  width: 400px;
  height: 100px;
}
.search-pokemon{
  text-align: center;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 30px;
}

.input-search-pokemon{
  width: 30vw;
  height: 50px;
  border: 2px solid #FDD824;
  border-radius: 8px;
  text-align: center;
  font-size: large;
  color: #656565;
}
.catalog-pokemon{
  width: 70vw;
  margin: 0 10px;
  background-color: hotpink;

  display: flex;
  flex-wrap: wrap;
}


.card-pokemon {
  background: #fff;
  border-radius: 4px;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.5);
  max-width: 400px;
  display: flex;
  flex-direction: row;
  border-radius: 25px;
  position: relative;
  margin: 10px 10px ;
}
.card-pokemon h2 {
  margin: 0;
  padding: 0 1rem;
}

.card-pokemon .desc {
  padding: 0.5rem 1rem;
  font-size: 12px;
}
.card-pokemon .actions {
  display: flex;
  justify-content: center;
}

.card-pokemon-text {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.title-card-pokemon {
  padding: 2.5em 1.5em 1.5em 1.5em;
}

.img-pokemon {
  width: 100%;
  height: 100%;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-position: bottom center;
  background-size: cover;
}

button {
  border: none;
  background: none;
  font-size: 24px;
  color: #656565;
  cursor: pointer;
  transition:.5s;
}

.cart-pokemon{
  width: 30vw;
  margin: 0 30px;
  min-height: 50px;
  background-color: indianred;
}
